.tips-dropdown {
    justify-content: center;
}
  
.tips-dropdown span {
    cursor: pointer;
    display: flex;
    flex-direction: row;
    padding-top: 10px;
    padding-bottom: 10px;
    justify-items: center;
}
  
.tips-dropdown .caret {
    font-size: 1.1em;
}
  
.tips-dropdown-content {
    margin-top: 5px;
}
  
@media (min-width: 768px) {
    .tips-dropdown span {
      font-size: 24px;
    }
  
    .tips-dropdown-content p {
      font-size: 18px;
      margin-top: -10px;
      text-align: left;
    }
}
